"use client";

import React from "react";

const TestsTable = ({ tests = [], renderActionColumn }) => {
  return (
    <div className="overflow-x-auto bg-white rounded-lg shadow">
      <table className="min-w-full text-sm">
        <thead className="bg-slate-100">
          <tr>
            <th className="px-4 py-2 text-left">Test Name</th>
            <th className="px-4 py-2 text-left">Patient</th>
            <th className="px-4 py-2 text-left">Doctor</th>
            <th className="px-4 py-2 text-right">Fee</th>
            <th className="px-4 py-2 text-center">Action</th>
          </tr>
        </thead>

        <tbody>
          {tests.length === 0 && (
            <tr>
              <td
                colSpan="5"
                className="px-4 py-6 text-center text-slate-500"
              >
                No tests found
              </td>
            </tr>
          )}

          {tests.map((test) => (
            <tr
              key={test.id}
              className="border-t hover:bg-slate-50"
            >
              <td className="px-4 py-2">{test.name}</td>
              <td className="px-4 py-2">{test.patient || "-"}</td>
              <td className="px-4 py-2">{test.doctor || "-"}</td>
              <td className="px-4 py-2 text-right">
                {test.fee || 0}
              </td>

              {/* âœ… ACTION COLUMN (NO UPDATE BUTTON) */}
              <td className="px-4 py-2 text-center">
                {renderActionColumn ? renderActionColumn(test) : null}
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default TestsTable;
